.works {
    width: 500px;
    position: relative;

    &__list {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        transform-style:  preserve-3d;
        perspective: 500px;
        >:not(:last-child) {
            margin-bottom: 7px; } }


    &__item {
        position: relative; }


    &__num {
        font-size: 0.7rem;
        font-weight: 700;
        text-transform: uppercase;
        position: absolute;
        width: 160px;
        overflow: hidden;
        border-radius: 5px 0 25px 0;
        top: -3px;
        left: -3px;
        background: $back-color;
        transition: box-shadow 0.3s 0.3s;
        cursor: pointer;
        padding: 5px 25px 5px 25px;
        box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.2);
        z-index: 4;
        &:hover {
            background: $hover-color; } }

    &__elem {
        position: relative;
        height: 1px;
        border-left: 10px solid $hover-color;
        border-bottom: 2px solid #082346;
        background: #6913349f;
        padding: 25px 10px 10px 10px;
        transition: visibility 0.1s, opacity 0.1s, width 0.8s 0.8s, height 0.8s;
        opacity: 1;
        visibility: visible;
        >:nth-child(1n) {
            opacity: 0;
            visibility: hidden;
            transition: opacity 0.1s, visibility 0.1s; }
        &:before, &:after {
            position: absolute;
            content: '';
            border-color: #082346;
            border-style: solid; }
        &:before {
            width: 10px;
            height: 10px;
            right: -2px;
            bottom: -2px;
            border-width: 0 2px 2px 0; }

        &:after {
            width: 5px;
            height: 5px;
            right: -2px;
            top: -2px;
            border-width: 2px 2px 0 0; } }

    &__description {
        color: #fff;
        height: 45px;
        font-size: 0.8rem;
        display: flex;
        margin-bottom: auto; }

    &__link {
        color: #fff;
        display: flex;
        justify-content: flex-end;
        width: 100%;
        a {
            color: #ffffff;
            &:hover {
                color: #3c3c3c; } } }

    .activ {
        margin-bottom: 5px;
        .works__elem {
            height: 100px;
            width: 100%;
            opacity: 1;
            visibility: visible;
            transition: visibility 0.8s, opacity 0.8s, width 0.1s, height 0.8s;
            margin-bottom: 5px;
            >:nth-child(1n) {
                opacity: 1;
                visibility: visible;
                transition: opacity 0.8s 0.4s, visibility 0.8s 0.4s; } }

        .works__num {
            &:hover {
                background: $back-color; } } } }
