.board {
    &__wrapper {
        margin: 0 10px; }
    &__inner {
        margin: 20px 0 0 0; }
    &__content {
        display: flex;
        position: relative; }
    &__sidebar {
        position: absolute;
        max-width: 276px;
        flex: 1 0 276px;
        padding: 20px;
        border: 3px solid $back-color;
        border-radius: 12px;
        background: #ffffff;

        opacity: 0;
        pointer-events: none;
        &-item {
            &:not(:last-child) {
                margin: 0 0 25px; } } }
    &__box {
        display: flex; }
    &__item {
        background: #845175;
        &:not(:last-child) {
            margin-right: 10px; }
        padding: 25px;
        max-height: 500px;
        min-height: 500px;
        border: 2px solid $back-color;
        &-title {
            font-weight: 400;
            font-size: 1rem;
            padding: 0 0 5px 0;
            color: $back-color; } } }

